<script>
export default {
  name: 'MenuItem',
  functional: true,
  props: {
    icon: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    a: {
      type: String,
      default: ''
    }

  },
  /**
   *
   * @param {*} h  h('div') /  h({template:"<span></span>"})  / h(()=>({template:"<span></span>"}))
   * @param {*} context 数据对象
   */
  render (h, context) {
    const { icon, title } = context.props
    const vnodes = []
    if (icon) {
      if (icon.includes('el-icon')) {
        vnodes.push(<i class={[icon, 'sub-el-icon']} />)
      } else {
        vnodes.push(<i class={[`icon-${icon}`, 'sub-el-icon iconfont']} />)
      }
    }

    if (title) {
      vnodes.push(<span slot='title'>{title}</span>)
    }

    // console.log(vnodes)
    return vnodes
  }
}
</script>

<style scoped>
.sub-el-icon {
  color: currentColor;
  width: 1em;
  height: 1em;
}
</style>
